<template>
  <div class="new">
    <Navbar />
    <div class="row">
      <div class="col-sm-3 p-0">
        <SideNavBar class="mobile-none" />
      </div>
      <Spinner v-if="isLoading" />
      <template v-else>
        <div class=" col-sm-9 p-0">
          <ProductList :products="products" />
        </div>
      </template>
    </div>
    <div class="footer mt-5">
      <FootNavBar class="mobile-show" />
      <Footer />
    </div>
  </div>
</template>

<script>
import Footer from "../components/Footer.vue";
import Navbar from "../components/Navbar.vue";
import SideNavBar from "../components/SideNavbar.vue";
import ProductList from "../components/ProductList.vue";
import FootNavBar from "../components/FootNavBar.vue";
import productsAPI from "./../apis/products.js";
import Spinner from "../components/Spinner.vue";

export default {
  components: { Navbar, SideNavBar, Footer, ProductList, FootNavBar, Spinner },
  name: "New",
  data() {
    return {
      products: [],
      isLoading: true,
    };
  },
  created() {
    this.fetchProducts();
  },
  methods: {
    async fetchProducts() {
      try {
        const response = await productsAPI.getProducts();
        this.products = response.data;
        this.isLoading = false;
      } catch (error) {
        this.isLoading = false;
        console.log("error", error);
      }
    },
  },
};
</script>

<style scoped></style>
